
<template>
  <div class="menuList">
    <el-menu
      default-active="1"
      class="el-menu-vertical-demo"
      unique-opened
      router
      @open="handleOpen"
      @close="handleClose"
      :default-openeds="openeds"
    >
      <div v-for="item in routersList" v-bind:key="item.path">
         <!--第一层-->
         <el-submenu index="1" class="listItem">
             <template slot="title">
                <span @click="toPath(item.path)">{{item.name}}</span>
             </template>
             <!-- 第二层 -->
             <div v-for="secondItem in item.children" v-bind:key="secondItem.path">
                <el-submenu v-if="secondItem.children && secondItem.children != []" :index="secondItem.path" class="listItem">
                    <template slot="title">
                       <span @click="toPath(secondItem.path)">{{secondItem.name}}</span>
                    </template>
                    <!-- 第三层 -->
                    <div v-for="threeItem in secondItem.children" v-bind:key="threeItem.path">
                      <el-menu-item :index="threeItem.path">
                       <template slot="title">
                          <span @click="toPath(threeItem.path)">{{threeItem.name}}</span>
                        </template>
                      </el-menu-item>
                    </div>
                </el-submenu>
               <el-menu-item v-else :index="secondItem.path"  @click="changeIcon(secondItem)">
                   <img :src="require(`../assets/image/subList${secondItem.sub}_${secondItem.icon}.png`)" >
                   <router-link :to="secondItem.path" tag="div">{{secondItem.name}}</router-link>
               </el-menu-item>
             </div>
        </el-submenu>
      </div>
    </el-menu>
  </div>
</template>
<script>
import fliterRouter from '@/utils/filterRouter'
export default {
  data() {
    return {
      input: "",
      routersList: [],
       isCollapse: true,
       color: 'router-link-active',
       openeds: ['1'],
       uniqueOpened: false,
    /*    vueRouterList:[{
         name:'名单管理',
         path:'/listAllocation',
         children:[
           {
             name:'名单分配',
             path:'/listAllocation',
             icon:'T',
             sub:1
            },{
             name:'名单回收',
             path:'/listRecovery',
             icon:'F',
             sub:2
            }
          ],
       },]
 */
    };
  },
  created() {
    this.routersList = fliterRouter
    let path = this.routersList[0].children[0].path;
    this.$router.push({ path: path}) 
   // console.log(fliterRouter)
  },
  methods: {
    changeIcon(item){
     let _picList =  this.routersList[0].children;
     if(_picList && _picList.length>0){
      _picList.forEach(it => {
        if(it.path == item.path){
             it.icon = 'T'
        }else{
            it.icon = 'F'
          }
        });
      }
    },
    toPath(path) {
     // console.log(path);
      this.$router.replace(path);
    },
    handleOpen(key, keyPath) {
      // console.log(key, keyPath);
    },
    handleClose(key, keyPath) {
      // console.log(key, keyPath);
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
body {
  margin: 0;
}
.flexRow {
  display: flex;
  flex-direction: row;
  align-items: center;
}
.menuList {
  width: 197px;
  overflow: hidden;
}
.userDiv {
  height: 77px;
  justify-content: center;
  border-bottom: 1px solid rgba(235, 235, 235, 1);

  margin-bottom: 15px;
}
.userDiv img {
  margin-right: 17px;
}
.el-row {
  width: 100%;
  height: 100%;
}
.el-col-24 {
  width: 100%;
  height: 100%;
}
.el-col-24 .el-menu {
  height: 100%;
  border: none;
  overflow: hidden;
}
.el-col-24 .el-menu .el-menu-item-group {
  padding-left: 20px;
}
.el-icon-arrow-down {
  background: url("../assets/image/down.png") no-repeat;
  background-size: 100% 100%;
  width: 10px;
  height: 5px;
}
.el-icon-arrow-down:before {
  content: none;
}
.menuList .el-menu-item {
  text-align: left;
  display: flex;
  align-items: center;
}
.menuList .el-menu-item  img{
  width: 16px;
  height: 16px;
  margin-left: -8px;
  margin-right: 6px;
}
.menuList .el-submenu .el-submenu__title {
  padding-left: 29px !important;
  text-align: left;
}
.el-submenu__title .el-submenu__icon-arrow {
  position: inherit;
  margin-left: 19px;
  top: 0;
  right: 0;
  margin-top: 0;
}
.el-submenu__title .el-menu--inline {
  overflow: hidden;
}
/* 左侧导航图片 */
.el-menu-item i {
  display: inline-block;
  width: 15px;
  height: 15px;
  background-size: 100% 100%;
  margin-right: 15px;
}
.el-menu-item.is-active i {
  display: inline-block;
  width: 15px;
  height: 15px;
  background-size: 100% 100%;
  margin-right: 15px;
}
.menuList .el-menu--inline {
  padding-left: 29px !important;
  overflow: hidden;
}
.el-menu-item.is-active{
  color: #39393C;
}
.router-link-active{
      color: #2085FF;
}
.el-submenu__title span{
  font-weight: 600;
}
</style>



