<template>
<div class="loginPage">
  <div class="logoTop flexRow">
   <div class="lpgoLeft">
     <img src="../assets/image/indexLogo.png">
   </div>
   <div class="lpgoRight flexRow">
      <span class="title">电商企业微信后台管理系统</span>
      <div class="outServer flexRow" @click="LogOut">
        <img src="../assets/image/out.png"/>
        <span>退出系统</span>
      </div>
   </div>

  </div>
  <div class="loginCon">
      <subMenu></subMenu>
      <el-col :span="24" class="content-wrapper">
        <transition name="fade" mode="out-in">
          <router-view></router-view>
        </transition>
      </el-col>
  </div>
  </div>


</template>
<script>
import subMenu from '@/components/subMenu.vue'
  export default {
    name:'index',
    data(){
      return {
         maxTime: 1800 * 1000, 
      }
    },
    components: {
      subMenu
    },
    created(){
    },
    methods: {
      LogOut(){
         this.$store.dispatch('FedLogOut').then(() => {//前端退出，无关后台
         this.$message({
          message: '退出成功',
          type: 'success'
        })
        this.$router.push({ path: '/' })
        }).catch(error =>{
           this.$message.error('退出失败');
        })
      } ,
    }
  }
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
body{
  overflow: hidden;
}
.flexRow{
  display: flex;
  flex-direction: row;
}
.loginPage{
  width: 100%;
  overflow: hidden;
  height: 100%;
  display: flex;
  flex-direction: column;
  background: rgba(247,247,247,1);
}
.logoTop{
  height: 70px;
  background: #518DF1;
}
.loginCon{
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: row;
}
.content-wrapper{
  flex: 1;
}
.lpgoLeft{
  width: 197px;
  display:flex;
  align-items: center;
  height: 100%;
 justify-content: center;
 background: #427AD6;
}
.lpgoLeft img{
  width: 142px;
  height: 40px;
}
.lpgoRight{
  display: flex;
  flex:1;
  height: 100%;
  justify-content: space-between;
  align-items: center;
  color: #fff;
      line-height: 70px;
}
.lpgoRight .title{
  font-size:20px;
  margin-left: 28.5px;
  font-weight: 600;
}
.lpgoRight .outServer{
  height: 100%;
  align-items: center;
}
.lpgoRight .outServer img{
  width: 16px;
  height: 16px;
}
.lpgoRight .outServer span{
  font-size:14px;
  margin: 0 20px 0 9px;
}

</style>



