<template>
  <div class="leftPage">
    <router-view></router-view>
  </div>
</template>

<script>
export default {
  data() {
    return {
      maxTime: 1800,
    };
  },
  created() {
    this.isTimeOut();
  },
  methods: {
    isTimeOut() {
      let clock = window.setInterval(() => {
        this.maxTime--;
        if (this.maxTime <= 0) {
          this.LogOut();
          clearInterval(clock);
        }
      }, 1000);
    },
    LogOut() {
      this.$store
        .dispatch("FedLogOut")
        .then(() => {
          //前端退出，无关后台
          this.$message({
            message: "退出成功",
            type: "success"
          });
          this.$router.push({ path: "/" });
        })
        .catch(error => {
          this.$message.error("退出失败");
        });
    }
  }
};
</script>

<style>
.leftPage {
  height: 100%;
}
</style>



