import request from '@/utils/request'
import qs from 'qs'

export function login(data) {
  return request({
    url: '/tpqydwt_app/dsqyh/pc/manage/loginInfo',
   // url:'/cntp_manager/manager/binding/queryAgentBinding/1',
    method: 'post',
    data:data
  })
}

export function getInfo(token) {
  return request({
    url: '/user/info',
    method: 'get',
    params: { token }
  })
}

export function logout() {
  return request({
    url: '/user/logout',
    method: 'post'
  })
}



