//import ListAllocation from '../views/MrosterManagment/listRecovery.vue' 
import IndexRpage from '../components/indexRpage.vue'
import Index from '../views/index.vue'
import Login from '../views/login.vue'

Vue.use(Router)
export default new Router({
  mode: "hash",//干掉地址栏里边的#号键
routes:[
  {path: '/',  name: '登录',component: Login,},
  {path: '/login', component: () => import('@/views/login'), name: '登录后台管理', hidden: true },
  {path: '/index',  name: '',title:'index',component: Index,
  children: [
    {
    path: '',
    component: IndexRpage,
    name: '名单管理',
    title:'名单管理',
    children:[
      { 
        title:'名单分配',
        name: '名单分配', 
        path: '/listAllocation', 
        component: () => import('@/views/MrosterManagment/ListAllocation'), 
       },
      { 
        path: '/listRecovery', 
        component: () => import('@/views/MrosterManagment/listRecovery'), 
        name: '名单回收', 
        title:'名单回收',
      },
      { 
        path: '/listIncloud', 
        component: () => import('@/views/MrosterManagment/listIncloud'), 
        name: '名单下发', 
        title:'名单下发',
      },
     
    ]
},
  ],
}

],
  })

