<template>
<div class="loginPage">
<div class="loginCon">
  <div class="cenDiv">
      <div class="formData">
        <div class="formDiv">
            <p class="loginLogo">
              <img src="../assets/image/loginLogo.png">
            </p>
            
            <el-form class="login-form" status-icon :rules="loginRules" ref="loginForm" :model="loginForm" label-width="0">
              <div class="errorDialog" v-if="errorDialog">
                <img src="../assets/image/errorPic.png">
                <span>用户名或密码输入错误，请重新输入</span>
            </div>
              <el-form-item prop="loginName" class="itemDiv">
                <el-input  class="itemInput"  @keyup.enter.native="handleLogin" v-model="loginForm.loginName" auto-complete="off" placeholder="请输入用户名"> 
                 <i slot="suffix" class="userImg el-input__icon el-icon-date"></i>
                </el-input>
              </el-form-item>
              <el-form-item prop="password" class="itemDiv">
                <el-input class="itemInput"  @keyup.enter.native="handleLogin" :type="passwordType" v-model="loginForm.password" auto-complete="off" placeholder="请输入密码">
                  <i slot="suffix" class="lockImg el-input__icon el-icon-date"></i>
                </el-input>
              </el-form-item>
            
              <el-button type="primary" size="small" @click.native.prevent="handleLogin" class="login-submit">登录</el-button>
            </el-form>
        </div>
      </div>
  </div>
</div>

<el-dialog
  title=""
  :visible.sync="dialogVisible"
  width="30%"
 >
  <img src="../assets/image/down.png">
  <span>此次为登陆错误提示文案</span>
  <span slot="footer" class="dialog-footer">
    <el-button type="primary" class="sureBtn" @click="dialogVisible = false">确 定</el-button>
  </span>
</el-dialog>

<el-dialog class="dialogTop"
  title="使用帮助"
  :visible.sync="dialogVisibleFlag"
  width="270px !important">
  <div>账户不存在</div>
  <span slot="footer" class="dialog-footer">
    <el-button type="primary" @click="dialogVisibleFlag = false">确 定</el-button>
  </span>
</el-dialog>

</div>


  
</template>

<script>
import { isvalidUsername } from '@/utils/validate'

export default {
  name: 'login',
  data() {
    return {
      loginForm: {
        loginName: '',//admin
        password: '',//123456
      },
      errorDialog:false,
      dialogVisible: false,
      dialogVisibleFlag: false,
      loginRules: {
        loginName: [
          { required: true, message: '用户名不可为空', trigger: 'blur' },
        ],
        password: [
          { required: true, message: '密码不可为空', trigger: 'blur' },
          { min: 6, message: '密码长度最少为6位', trigger: 'blur' }
        ],
      },
      passwordType: 'password'
    }
  },
  created() {
  },
  mounted() {},
  computed: {
  },
  props: [],
  methods: {
    showPassword() {
      this.passwordType === ''
        ? (this.passwordType = 'password')
        : (this.passwordType = '')
    },
    handleLogin(){
       this.$refs.loginForm.validate(valid => {
         if (valid) {
           this.$store.dispatch('Login', this.loginForm).then(res => {
             if(res && res.code == '000'){
               sessionStorage.setItem('userInfo', JSON.stringify(res.user));
                  this.$router.push({ path: '/index' })
             }
           }).catch(error =>{
              this.errorDialog = true;
           })
        }
       })
    },
  }
}
</script>
<style scoped>
html,body{
  height: 100%;
  width: 100%;
}
p{
  margin: 0;
  padding: 0;
}
.loginPage{
  height: 100%;
  width: 100%;
  background: url('../assets/image/loginBg.png') no-repeat;
  background-size: 100% 100%; 
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
}

.loginPage .loginCon{
   width: 100%;
   height: 100%;
}
.loginPage .loginCon .cenDiv{
   height: 100%;
   position: relative;
    background: url('../assets/image/loginCenBg.png') no-repeat;
       background-size: 100% 100%; 
}

.loginCon .cenDiv .formData{
    position: absolute;
    top: 34px;
    right: 34px;
    width: 812px;
    height: 842px;
    z-index: 100;
}
.formDiv{
  height: 100%;
  background: #fff;
  overflow: hidden;
  margin: 0 auto;
  z-index: 100;
}
.formDiv .loginLogo{
  width: 353px;
  height: 116px;
  margin: 159px auto 0;
}
.formDiv .loginLogo img{
  width: 100%;
  height: 100%;
}
.formDiv .login-form{
  width: 450px;
  margin: 128px auto 0;
  position: relative;
}
.formDiv .el-form-item__content{
border:1px solid rgba(205,209,213,1);
border-radius:4px;
}
.login-form .el-form-item--small{
  margin-bottom: 40px;

}
.itemDiv /deep/ .el-input__inner{
  width: 450PX;
  height: 60px;
  font-size: 18px;
}
.cenDiv input:-webkit-autofill{
   background-color: #fff;
}
.cenDiv /deep/.formDiv .el-input__inner{
    padding: 0 0 0 59px;
}
.el-icon-date{
  width: 18px;
  height: 20px;
}

.formDiv .login-form .itemDiv .userImg{
  background: url('../assets/image/userName.png') no-repeat;
}
.formDiv .login-form .itemDiv .lockImg{
  background: url('../assets/image/loginLock.png') no-repeat;
}

.el-button[data-v-7589b93f] {
   width: 450px;
  height: 60px;
  padding: 0;
  font-size:17px;
color:rgba(255,255,255,1);
margin-top: 25px;
} 
.formDiv  .errorDialog{
  width: 448px;
  height: 50px;
  margin: 0 auto;
  background:rgba(255,249,249,1);
border:1px solid rgba(232,48,48,1);
border-radius:4px;
display: flex;
align-items: center;
position: absolute;
top:-72px;
}
.formDiv  .errorDialog img{
  width: 16px;
  height: 16px;
  margin: 0 22px 0 25px;
}
.formDiv  .errorDialog span{
  font-size:16px;
  color:rgba(232,48,48,1);
}
 .el-dialog{
  width: 310px !important;
  height: 190px;
  overflow: hidden;
}
.el-dialog__body{
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 30px;
  padding: 0;
}
.el-dialog__body img{
width: 40px;
height: 40px;
margin-bottom: 23px;
}
.el-dialog__body span{
  font-size:14px;
  color:rgba(124,124,124,1);
}
.el-dialog__header{
  padding: 0;
}
.el-dialog__footer{
  padding: 0;
  text-align: center;
}
.sureBtn{
  width: 200px;
  height: 32px;
  line-height: 32px;
  font-size:14px;
  margin-top: 26px;
}
.dialogTop .el-dialog__header{
  display: flex;
  flex-direction: row;
  align-items: center;
  height: 21px;
  background: #518DF1;
  justify-content: space-between;
}
.dialogTop .el-dialog__title{
font-size:8px;
color:rgba(255,255,255,1);
margin-left: 10px;
}
.dialogTop .el-dialog__header .el-dialog__headerbtn{
  top:0;
  position: inherit;
}
.dialogTop .el-dialog__headerbtn i{
  color: #fff;
  margin-right: 6px;
} 
.formDiv .el-button{
  width: 100%;
  font-size: 17px;
}
.cenDiv .formDiv /deep/.el-icon-circle-close{
  display: none;
}
.cenDiv .formDiv /deep/.el-input__icon{
  display: none;
}
.cenDiv /deep/.el-form-item--feedback .el-input__validateIcon{
  display: none;
}
.itemDiv .el-input /deep/ .el-input__inner{
      padding-left: 50px;
    }
.cenDiv .formDiv /deep/ .el-form-item__error{
  position: absolute;
  top:66px;
  left:59px;
  font-size:16px;
  color:rgba(232,48,48,1);
}
@media screen and (max-width: 1680px){
   .loginPage .loginCon{ height: 100%;
     padding: 86px 110px;
     z-index: 100;
    }
    .loginPage .loginCon .cenDiv{
       width: 100%;
       height: 100%;
       background: url('../assets/image/loginCenBg.png') no-repeat;
       background-size: 100% 100%; 
    }
    .loginCon .cenDiv .formData{
      height: 100%;
      width: 580px;
      top: 25px;
      right: 12px;
    }
    .formDiv .loginLogo{
          margin: 100px auto 0;
    }
    .formDiv .login-form{
      margin: 80px auto 0px;
    }
    .login-form .el-form-item--small{
      margin-bottom: 30px;
    }
    .itemDiv /deep/ .el-input__inner{
      height: 50px;
      width: 450px;
      font-size: 15px;
      padding-left: 50px;
    }
    .cenDiv .formDiv /deep/ .el-form-item__error{
      font-size: 12px;
      top: 55px;
    }
    .el-button{
      height: 50px !important;
    }
    .formDiv .errorDialog{
      height: 40px;
      top: -55px;
    }
    .formDiv .errorDialog span{
      font-size: 12px;
    }
}

</style>



